@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
* {
  max-width: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  color: #111111;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
@media (min-width: 768px) {
  body {
    min-width: 768px;
  }
}
@media (min-width: 1300px) {
  body {
    min-width: 1300px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.btn {
  display: flex;
  box-sizing: border-box;
  width: 280px;
  min-height: 40px;
  padding-top: 0.625em;
  padding-bottom: 0.625em;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background: #68b738;
  justify-content: center;
  align-items: center;
}
.btn:hover {
  background: #5eaa2f;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.btn--secondary {
  background: #f2f2f2;
  color: #444444;
}
.btn--secondary:hover {
  background: #ebebeb;
}
.btn--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .btn {
    width: 249px;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    font-size: 16px;
    line-height: 20px;
  }
}
.page-header {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  min-width: 320px;
  min-height: 65px;
  background-color: #ffffff;
  align-items: center;
}
.page-header__logo-link {
  margin-left: 0;
  display: flex;
  min-height: 65px;
  min-width: 320px;
  justify-content: flex-start;
  align-items: center;
}
.page-header__logo-link[href]:hover {
  opacity: 0.8;
}
.page-header__logo-link[href]:active {
  opacity: 0.6;
}
.page-header__logo-image {
  margin-left: 20px;
}
.page-header__menu-btn--show {
  position: absolute;
  display: block;
  top: 23px;
  right: calc(50% - 160px + 20px);
  width: 24px;
  height: 18px;
  background: url("../img/icon-mobile-menu.svg");
  border: none;
  font-size: 0;
  cursor: pointer;
}
.page-header__menu-btn--close {
  position: absolute;
  display: block;
  top: 23px;
  right: calc(50% - 160px + 23px);
  width: 18px;
  height: 18px;
  background: url("../img/icon-close.svg");
  border: none;
  font-size: 0;
  cursor: pointer;
}
.page-header__menu-btn--hide {
  display: none;
}
@media (min-width: 768px) {
  .page-header {
    padding-left: calc(50% - 384px);
    padding-right: calc(50% - 384px);
    padding-top: 20px;
    justify-content: space-between;
    flex-wrap: nowrap;
  }
  .page-header__menu-btn--show,
  .page-header__menu-btn--close {
    display: none;
  }
  .page-header__logo-link {
    margin-left: 8px;
    min-width: 230px;
    width: 230px;
  }
  .page-header__logo-image {
    margin-left: 12px;
    margin-top: 2px;
    height: 58px;
  }
  .page-header__nav,
  .page-header__nav--hide {
    display: flex;
  }
  .page-header__menu-btn,
  .page-header__close-btn {
    display: none;
  }
}
@media (min-width: 1300px) {
  .page-header {
    padding-top: 48px;
    padding-left: calc(50% - 650px + 40px);
    padding-right: calc(50% - 650px + 40px);
