  .page-footer__address p {
    text-align: left;
  }
  .page-footer__map {
    min-height: 400px;
    margin-left: 0;
    margin-right: 0;
  }
  .page-footer__logo-link {
    width: 150px;
    margin-left: calc(50% - 650px + 40px);
    padding-left: 0;
    padding-top: 62px;
  }
}
