.example {
  width: 100%;
  margin: 0;
  padding-bottom: 22px;
  padding-left: calc(50% - 160px + 20px);
  padding-right: calc(50% - 160px + 20px);
  display: flex;
  align-items: center;
  box-sizing: border-box;
  flex-direction: column;
  background-color: #f2f2f2;
}
.example__heading {
  margin-top: 16px;
  margin-bottom: 24px;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  align-self: flex-start;
}
.example__text {
  margin-top: 0;
  margin-bottom: 1.8em;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  padding-right: 2px;
  line-height: 18px;
  letter-spacing: 0;
  color: #444444;
}
.example__stats {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.example__stats tbody {
  display: flex;
  flex-wrap: wrap;
}
.example__stats-weight,
.example__stats-time {
  position: relative;
  width: 124px;
  min-height: 56px;
  display: flex;
  text-transform: uppercase;
}
.example__stats-weight {
  margin-right: auto;
}
.example__stats-time {
  margin-left: auto;
}
.example__stats-cost {
  margin-top: 32px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.example__stats-cost td,
.example__stats-cost th {
  font-size: 14px;
  line-height: 20px;
  font-weight: normal;
  text-transform: uppercase;
}
.example__stats-weight td,
.example__stats-time td {
  width: 124px;
  min-height: 56px;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  font-size: 24px;
  line-height: 24px;
  border: 1px solid #cfcfcf;
  border-radius: 2px;
  text-align: center;
}
.example__stats-weight th,
.example__stats-time th {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 60%);
  padding-left: 15px;
  padding-right: 15px;
  text-align: center;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: normal;
  text-transform: lowercase;
  background-color: #f2f2f2;
}
.example__slider {
  position: relative;
  margin-top: 20px;
  width: 100%;
  min-height: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.example__progress {
  position: relative;
  min-height: 240px;
  width: 100%;
}
.example__controls {
  position: relative;
  margin-top: 32px;
  width: 80%;
  min-height: 20px;
  padding-left: 12px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.example__controls-range {
  margin-left: 20px;
  margin-right: 20px;
}
.example__range {
  margin: 0;
  box-sizing: border-box;
  padding-top: 0;
  padding-bottom: 0;
  appearance: none;
  /* Hides the slider so that custom slider can be made */
  width: 82px;
  height: 14px;
  padding-left: 2px;
  padding-right: 2px;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 6px;
  /* Otherwise white in Chrome */
}
.example__range:focus {
  outline: none;
  /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}
.example__range::-webkit-slider-thumb {
  margin-top: 3px;
  appearance: none;
  border: none;
  width: 35px;
  height: 6px;
  border-radius: 3px;
  background: #68b738;
  cursor: pointer;
}
/* All the same stuff for Firefox */
.example__range::-moz-range-thumb {
  appearance: none;
  border: none;
  width: 35px;
  height: 6px;
  border-radius: 3px;
  background: #68b738;
  cursor: pointer;
}
/* All the same stuff for IE */
.example__range::-ms-thumb {
  appearance: none;
  border: none;
  width: 35px;
  height: 6px;
  border-radius: 3px;
  background: #68b738;
  cursor: pointer;
}
.example__range::-webkit-slider-runnable-track {
  appearance: none;
  width: 70px;
  height: 12px;
  padding-left: 5px;
  padding-right: 5px;
  background-color: #ffffff;
  border: none;
  border-radius: 5px;
  opacity: 1;
}
.example__range:focus::-webkit-slider-runnable-track {
  background: #ffffff;
}
.example__range::-moz-range-track {
  appearance: none;
  width: 60px;
  height: 12px;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #ffffff;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  opacity: 0;
}
.example__range::-ms-track {
  appearance: none;
  width: 70px;
  padding-left: 10px;
  padding-right: 10px;
  height: 12px;
  border-color: transparent;
  color: transparent;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  opacity: 0;
}
.example__range::-ms-fill-lower {
  background: #dcdcdc;
}
.example__range:focus::-ms-fill-lower {
  background: #ffffff;
}
.example__range::-ms-fill-upper {
  background: #ffffff;
}
.example__range:focus::-ms-fill-upper {
  background: #ffffff;
}
.example__label-past,
.example__label-now {
  cursor: pointer;
}
.example__before {
  position: absolute;
  display: block;
  left: -20px;
  top: -20px;
  max-width: 320px;
  width: 320px;
